<!-- 购物车模板规格选择 2018.12.20 李萌-->
<template>
	<div class="product_item" @click="elect">
		<div class="p-head">
			<div class="smimg">
				<img :src="prod.img?(config.cdnbase+prod.img):(specs.list?config.cdnbase+specs.list[0].img:'')">
			</div>
			<div class="pnp">
				<div class="p-price">
					<div class="p-money">
						<span>{{lang('价格')}}：</span>
						<span class="rmb">{{data.money}}</span>
						<span v-if="promotion_flag">
							<span class="p-original-price" v-if="prod.price">{{prod.price}}</span>
							<span class="p-original-price" v-else="">{{data.price-data.time_price}}</span>
						</span>
						<span v-else>
							<span class="p-original-price" v-if="prod.price">{{prod.price}}</span>
							<span class="p-original-price" v-else="">{{data.price}}</span>
						</span>
						<del>
							<span class="rmb">{{data.money}}{{data.oldprice}}</span>
						</del>
					</div>
					<div class="p-spec">
						<span>{{lang('已选')}}：</span>
						<span v-if="prod.specname">{{prod.specname}} {{prod.option1}} {{prod.option2}}</span>
						<span v-else>{{lang('请选择规格')}}</span>
					</div>
				</div>
			</div>
		</div>
		<div class="p-spec-content">
			<SpecItem
					:prod="prod"
					:index1="0"
					:specs="specs"
					:label_name="data.option1_name"
					:data="data"
					:promotion_flag="promotion_flag"
					:lang="data.lang"
			></SpecItem>
			<OptionItem
					:prod="prod"
					:index1="0"
					:specs="specs"
					:label_name="data.option2_name"
					:option_index="1"
					:promotion_flag="promotion_flag"
					:data="data"
					:lang="data.lang"
			></OptionItem>
			<OptionItem
					:prod="prod"
					:index1="0"
					:specs="specs"
					:label_name="data.option3_name"
					:option_index="2"
					:promotion_flag="promotion_flag"
					:data="data"
					:lang="data.lang"
			></OptionItem>
		</div>
		<div class="p-count p-commen">
			<div class="num color">{{lang('数量')}}</div>
			<div class="mdui-numbox" data-numbox-step="1" data-numbox-min="1" data-numbox-max="999">
				<button class="mdui-numbox-btn mdui-numbox-btn-minus" @click="decNum()">
					<i class="mdui-icon material-icons">remove</i>
				</button>
				<input
						class="mdui-numbox-input"
						type="number"
						:value="prod.number"
						onfocus="this.blur()"
						readonly
				>
				<button class="mdui-numbox-btn mdui-numbox-btn-plus" @click="addNum()">
					<i class="mdui-icon material-icons">add</i>
				</button>
			</div>
		</div>
		<!--<div class="num">-->
		<!--<span>数量</span>-->
		<!--<p>-->
		<!--<span @click="decNum()" class="dec_btn"></span>-->
		<!--<span class="prod_num">{{prod.number}}</span>-->
		<!--<span @click="addNum()" class="add_btn"></span>-->
		<!--</p>-->
		<!--</div>-->
	</div>
</template>
<script>
  import SpecItem from './spec_item'
  import OptionItem from './option_item'
  import { mapState } from "vuex";
  export default {
    name: "spec_spa",
    props: ['data', 'cart', 'specs', 'prod', 'gifts', 'promotion_flag'],
    data(){
      return {
        priceoff: [],
        saleoff: [],
        manyoff: [],
        manyoff_new: []
      }
    },
    components: {
      SpecItem,
      OptionItem,
    },
    methods: {
      elect() { //传参方法
        this.$emit('prod', this.prod); //第一个参数是父组件中v-on绑定的自定义回调方法，第二个参数为传递的参数
      },
      decNum: function () {
        this.prod.number > 1 ? this.prod.number -= 1 : this.prod.number = 1;
        for (let i in this.gifts) {
          for (let j in this.gifts[i]) {
            this.gifts[i][j].number > 1 ? this.gifts[i][j].number -= 1 : this.gifts[i][j].number = 1;
          }
        }
      },
      addNum: function () {
        this.prod.number += 1;
        for (let i in this.gifts) {
          for (let j in this.gifts[i]) {
            this.gifts[i][j].number += 1;
          }
        }
      },
    },
    computed: {
      ...mapState(["language","config"]),
      lang() {
        return function(src) {
          return this.language ? this.language[src] : "";
        };
      }
    }
  }
</script>

<style scoped>
	.product_item {
		padding: 10px 0;
	}

	.smimg {
		width: 25%;
	}

	.smimg img {
		width: 80%;
	}
</style>
