<template>
	<div class="spec_List user-name">
		<div class="spec_Title p-color p-commen">
			<div class="color"> {{label_name}}</div>
			<div class="color-item font-color" v-for="(item,index) in promotion_info" :key="index" style="font-style:normal;margin-left:10px;">
				<span v-if="(index1+1)===item.count" >{{item.name}}</span>
			</div>
		</div>
		<div class="spec_Item opt1 ks" @click="select_specitem(item)" v-for="(item,index) in specs.name_list" :key="index">
			<div class="sticky"></div>
			<span :class="{active:active1(item,'specname'),disable:disable(item,'option1_list')}" class="spec_name param-item font-color">{{item.name}}</span>
		</div>
	</div>
</template>
<script>
	export default {
		name: "spec_group",
		props: ['prod', 'label_name', 'index1', 'specs', 'promotion_info', 'lang', 'promotion_flag', 'data'],
		computed: {
			active1() {
				return function (item, prop_name) {
					return this.prod[prop_name] === item.name
				}
			},
			disable() {
				return function (item, prop_name) {
					return this.specs[prop_name].length > 0 && item.inventory === 0 && this.specs['option1_list'].length === 0;
				}
			}
		},
		mounted() {
			for (var i in this.specs.name_list) {
				var spec = this.specs.name_list[i];
				if (spec.isdefault === 1) {
					this.select_specitem(spec);
					break;
				}
			}
		},
		methods: {
			select_specitem: function (item) {
				this.prod.specname = item.name;
				this.prod.img = item.img;
				this.prod.option1 = "";
				this.prod.option2 = "";
				this.prod.sku = "";
				if (this.specs.option1_list.length === 0) {
					this.prod.price= this.promotion_flag?item.price - this.data.time_price:item.price;
					this.prod.sku = item.id;
					this.prod.specid = item.id;
				}else{
					for (let i in this.specs.list) {
						let oneSpec = this.specs.list[i];
						if (oneSpec.name + '' === this.prod.specname + '') {
							if(oneSpec.option1 ==='' || oneSpec.option1 === null){
								this.prod.price= this.promotion_flag?this.specs.list[i].price - this.data.time_price:this.specs.list[i].price;
								this.prod.sku = this.specs.list[i].id;
								this.prod.specid = this.specs.list[i].id;
							}
						}
					}
				}
			}
		}
	}
</script>

<style scoped>
    .spec_Title {
        padding: 5px 0;
    }

    .spec_Item {
        display: inline-block;
    }

    .spec_Img {
        width: 100%;
    }

    .spec_Img img {
        max-width: 100%;
        height: 100px;
    }

    .spec_name {
        overflow: hidden;
        display: block;
        word-break: break-all;
    }
    .disable {
        background: #efefef;
        color: #999;
        box-shadow: none;
        cursor: not-allowed !important;
    }
    .active1 {
        border: 1px solid red !important;
    }

    .active2 {
        border: 1px solid red !important;
    }
</style>
