<template>
  <div id="option_item">
    <div
      class="spec_Title p-color p-commen"
      v-if="!specs['option'+option_index+'_key_list']||specs['option'+option_index+'_key_list'].length"
    >
      <div class="color">{{label_name}}</div>
    </div>
    <div class="spec_List option1 xh spec1">
      <div
        class="option_Item opt2 font-color"
        v-for="(name,index) in specs['option'+option_index+'_key_list']"
        @click="select_option(name)"
        :class="{active:active1(name), disable:isdisable(name)}"
        :disabled="isdisable(name)"
        :key="index"
      >
        <span>
          {{name}}
          <span v-if="stockstate(name)" class="oos"></span>
        </span>
      </div>
    </div>
  </div>
</template>
<script>
	export default {
		props: {
			prod: {
				type: Object
			},
			index1: {
				type: Number
			},
			specs: {
				type: Object
			},
			label_name: {
				type: String
			},
			option_index: {
				type: Number
			},
			lang: {
				type: Object
			},
			promotion_flag:{
				type: Boolean
			},
			data:{
				type: Object
			}
		},
		computed: {
			active1(){
				return function (name) {
					let item = this.getItem(name);
					if (!item) return false;
					return this.prod['option' + this.option_index] === item['option' + this.option_index];
				}
			},
			isdisable(){
				return function (name) {
					let item = this.getItem(name);
					if (!item) return true;
					let disable = false;
					if (this.option_index === 1) {
						disable = !this.prod.specname || (item.inventory === 0 && this.specs['option2_list'].length === 0);
					}
					else {
						disable = !this.prod.option1 || item.inventory === 0;
					}
					return this.getDisableState(item) || disable;
				}
			},
			stockstate(){
				return function (name) {
					let item = this.getItem(name);
					if (!item) return false;
					if (this.option_index === 1) {
						if (this.specs['option2_list'].length === 0)
							return item.inventory === 0;
						else
							return false;
					} else
						return item.inventory === 0;
				}
			}
		},
		mounted(){
			this.default_click();
		},
		methods: {
			//默认点击事件
			default_click: function () {
				if (this.option_index === 1) {
					let arr = [], option1_list = [];
					for (let i in this.specs.list) {
						if (this.prod.specname === this.specs.list[i].name) {
							if (option1_list.indexOf(this.specs.list[i].option1) < 0) {
								arr.push(i);
								option1_list.push(this.specs.list[i].option1);
							}
						}
					}
					if (arr.length === 1) {
						this.select_option(this.specs.list[arr[0]].option1);
						arr = null;
					}
				} else if (this.option_index === 2) {
					let arr = [], option1_list = [];
					for (let i in this.specs.list) {
						if (this.prod.specname === this.specs.list[i].name && this.prod.option1 === this.specs.list[i].option1) {
							if (option1_list.indexOf(this.specs.list[i].option1) < 0) {
								arr.push(i);
								option1_list.push(this.specs.list[i].option1);
							}
						}
					}
					if (arr.length === 1) {
						this.select_option(this.specs.list[arr[0]]);
						arr = null;
					}
				}
			},
			select_option: function (name) {
				let item = this.getItem(name);
				if (!item)return;
				this.prod['option' + this.option_index] = item['option' + this.option_index];
				if (this.option_index === 1) {
					this.prod.option2 = "";
					this.prod.price = "";
					this.prod.sku = "";
					if (this.specs.option2_list.length === 0) {
						this.prod.price= this.promotion_flag?item.price - this.data.time_price:item.price;
						this.prod.sku = item.id;
						this.prod.specid = item.id;
					}else{
            for (let i in this.specs.list) {
              let oneSpec = this.specs.list[i];
              if (oneSpec.name + '' === this.prod.specname + '' &&
                oneSpec.option1 + '' === this.prod.option1 + '') {
                if(oneSpec.option2 ==='' || oneSpec.option2 === null){
                  this.prod.price= this.promotion_flag?this.specs.list[i].price - this.data.time_price:this.specs.list[i].price;
                  this.prod.sku = this.specs.list[i].id;
                  this.prod.specid = this.specs.list[i].id;
                }
              }
            }
          }
				} else if (this.option_index === 2) {
					this.prod.price=this.promotion_flag?item.price - this.data.time_price:item.price;
					this.prod.sku = item.id;
					this.prod.specid = item.id;
				}
			},
			getDisableState: function (item) {
				if (this.option_index === 1) {
					return this.prod.specname !== item.name;
				} else {
					return this.prod.specname !== item.name || this.prod.option1 !== item.option1;
				}
			},
			getItem: function (name) {
				for (let option of this.specs.list) {
					if (this.prod.specname === option.name) {
						const where = this.option_index === 1 ? true : option['option1'] === this.prod['option1'];
						if (where && option['option' + this.option_index] === name) {
							return option;
						}
					}
				}
				return null;
			}
		}
	}
</script>

<style scoped>
  .spec_Title{
    padding: 5px 0;
  }
  .spec_List .option_Item{
    display: inline-block;
    padding: 10px;
      border: none;
  }
  .disable{
    background: #eee !important;
    color: #999;
    box-shadow: none;
    cursor: not-allowed !important;
    border-style: dashed !important;
  }
  .oos{
    color:#ff8583
  }
  .active1{
    border:1px solid red !important;
  }
  .active2{
    border:1px solid red !important;
  }
</style>
