<!-- 购物车模板规格 2018.12.20 李萌 -->
<template>
  <div id="spec_cart">
    <Spec_spa_cart
      :data="data"
      :cart="cart"
      :specs="specs"
      :prod="prod"
      @prod="elect"
      :gifts="data.gifts"
      :promotion_flag="promotion_flag"
    ></Spec_spa_cart>
    <div class="buy-product">
      <div class="add-car disable" @click="add_cart()">{{lang('确定')}}</div>
    </div>
  </div>
</template>
<script>
	import Spec_spa_cart from './spec/spec_spa_cart'
    import {mapState} from 'vuex'

	export default {
		props: {
			data: {
				type: Object
			},
			cart: {
				type: Object
			},
			specs: {
				type: Object
			},
			promotion_flag: {
				type: Boolean
			}
		},
		components: {Spec_spa_cart},
		data () {
			return {
				prod: {
					goods_id: '',
					tid: parseInt(this.$router.history.current.params.typeid),
					classgoods_id: '',
					sourceid: '',
					userkey: '',
					name: '',
					specid: 0,
					specname: '',
					sku: '',
					price: 0,
					img: '',
					option1: '',
					option2: '',
					number: 1,
					checked: true,
					priceflag: ''
				},
				cart_win: false,
				gifts: [],
				spec_win: this.specWin,
			}
		},
		mounted () {
    },
		methods: {
			elect (prod) { // 传参方法
				this.$emit('prod', prod) // 第一个参数是父组件中v-on绑定的自定义回调方法，第二个参数为传递的参数
			},
			add_cart () {
        if (!this.prod.sku || this.prod.price === 0) {
          if (this.specs.option1_key_list.length !== 0) {
            if (!this.prod.option1 === '') {
              this.$toast.bottom(this.lang('请您选择商品的颜色和规格'))
              return
            }
          }
          if (this.specs.option2_key_list.length !== 0) {
            if (!this.prod.option2 === '') {
              this.$toast.bottom(this.lang('请您选择商品的颜色和规格'))
              return
            }
          }
          this.$toast.bottom(this.lang('请您先选择商品'))
					return
				}
				// 判断是否缺货
				let GoodsSpecs = this.specs.list;
				for (let j in GoodsSpecs) {
					let spec = GoodsSpecs[j];
					if (this.prod.sku === spec.id) {
						if (spec.inventory === 0) {
							this.$layer.msg(this.data.lang('缺货中，请选择其他商品'));
							return;
						}
					}
				}
				this.$toast.bottom(this.lang('已加入到购物车'))
				this.$emit('cartwin')
				this.prod.goods_id = this.data.goods_id
				this.prod.classgoods_id = this.data.id
				this.prod.sourceid = this.data.source
				this.prod.userkey = this.data.userkey
				this.prod.name = this.data.sitename
				this.prod.oldprice = this.data.oldprice
				this.prod.money = this.data.money
				this.prod.priceflag = this.promotion_flag ? 1 : 0
				let date = new Date()
				let min = date.getMinutes()
				date.setMinutes(min + 20)
				this.cart.add_time = date

				if (this.cart.list.length === 0) {//当购物车为空
					this.$set(this.cart.list, 0, this.prod);
					this.initialize();
					return
				} else if (this.cart.list.length > 0) {
					for (let n = 0; n < this.cart.list.length; n++) {
						if (this.cart.list[n].specname === this.prod.specname && this.cart.list[n].option1 === this.prod.option1 && this.cart.list[n].option2 === this.prod.option2 && this.cart.list[n].priceflag === this.prod.priceflag) {//当所选商品与购物车商品重合
							let number = this.cart.list[n].number
							this.cart.list.splice(n,1)
							this.prod.number = this.prod.number + number
							this.cart.list.push(this.prod)
							this.initialize();
							return;
						} else {//当所选商品与购物车商品不重合
							if (n === this.cart.list.length - 1) {//确定当前循环完毕
								this.$set(this.cart.list, this.cart.list.length, this.prod);
								this.initialize();
								return
							}
						}
					}
				}
			},
			initialize(){
				if (this.cart) {
					localStorage.setItem("cart_info", JSON.stringify(this.cart));
				}
				this.prod = {//初始化prod
					tid: parseInt(this.$router.history.current.params.typeid),
					specid: 0,
					sourceid: this.data.source,
					classgoods_id: '',
					userkey: this.data.userkey,
					name: '',
					specname: '',
					sku: '',
					price: 0,
					img: '',
					option1: '',
					option2: '',
					number: 1,
					checked: true,
					priceflag:'',
				};
			}
		},
		computed: {
          ...mapState(['language']),
          lang(){
            return function (src) {
              return this.language ? this.language[src] : ''
            }
          },
			allnum(){
				let allnum = 0;
				for (let item of this.cart.list) {
					allnum += item.number;
				}
				return allnum;
			}
		},
		watch: {}
	}
</script>

<style scoped>
	.cart-box-fixed {
		display: none;
	}
	.btn {
		font-size: 14px;
		height: 50px;
		line-height: 50px;
		text-align: center;
	}
</style>
