<template>
	<div class="app-detail" id="#top" v-cloak>
		<div v-show="!isShow">
			<!--头部-->
			<header>
				<header-top>
					<img width="28" height="28" src="../../assets/image/detail/10.png" alt="menu" class="icon-menu" slot="menu" @click="goBack()">
					<img width="28" height="28" src="../../assets/image/footprint.png" alt="logo" class="logo" slot="home" @click="foot_flag=true;">
				</header-top>
			</header>
			<div>
				<!--轮播图-->
				<div v-cloak id="swipe" v-if="goodVisibility">
					<div class="swiper-container">
						<div class="swiper-wrapper">
							<div class="swiper-slide blue-slide"  v-if="detail_goods.head_video" v-html="detail_goods.head_video"></div>
							<div class="swiper-slide blue-slide" v-for="(banner,index) of detail_goods.head_imgs" :key="index">
								<img :src="banner?config.cdnbase+banner:''"/>
							</div>
						</div>
						<div class="pagination"></div>
					</div>
				</div>
				<!--倒计时，价格-->
				<div class="detail_price mdui-row mdui-p-y-1 detail_count_down ">
					<div class="mdui-typo-body-2 product-price mdui-text-color-white mdui-col-xs-7 mdui-p-x-0"><span
							class="price1 mdui-typo-headline "><span
							class="mdui-typo-subheading">{{detail_goods.money}}</span>{{detail_goods.price - detail_goods.time_price}} <span
							v-if="detail_goods.price>=0&&detail_goods.time_price>=0">~</span> {{detail_goods.price}}</span><span
							class="price2"><del><span>{{detail_goods.money}}</span>{{detail_goods.oldprice}}</del></span>
						<div class="price3">{{detail_goods.saleno}}{{lang('人已购买')}}</div>
					</div>
					<div class="mdui-typo-body-2 product-time mdui-promotions mdui-col-xs-5 mdui-p-x-0 mdui-text-right" v-if="cdown_flag">
						<span class="mdui-date-text">{{lang('距离结束还剩')}}:</span>
						<div class="mdui-date">
							<span class="leftDay" style="">{{d}}</span>
							<span class="leftDayName" style="">{{lang('天')}}</span>
							<span class="leftHours">{{h}}</span>
							<span class="point">:</span>
							<span class="leftMinutes">{{m}}</span>
							<span class="point">:</span>
							<span class="leftSecond"><span class="leftSeconds">{{s}}</span></span>
						</div>
					</div>
				</div>
				<!--名称介绍-->
				<div class="p-detail">
					<h3 class="p-name">{{detail_goods.sitename}}</h3>
					<p class="p-title">{{detail_goods.promotion_name1}} {{detail_goods.promotion_name2}}</p>
					<div class="product-tag"  v-if="detail_goods.type">
						<span v-if="detail_goods.type[0].fathername"><router-link :to="'/home/'+$router.history.current.params.typeid">{{detail_goods.type[0].fathername}}<i class="mdui-icon material-icons">keyboard_arrow_right</i></router-link></span>
						<span v-if="detail_goods.type[1].fathername"><router-link :to="'/home/'+$router.history.current.params.typeid">{{detail_goods.type[1].fathername}}<i class="mdui-icon material-icons">keyboard_arrow_right</i></router-link></span>
					</div>
				</div>
				<!--运输图-->
				<div class="mdui-row mdui-color-white mdui-m-t-1 mdui-pannel supply-detail">
					<p>{{lang('7天鉴赏期')}}</p>
					<p>{{lang('邮费全免，配送时间5-7个工作日')}}</p>
					<div class="supply-mes">
						<ul>
							<li><img src="../../assets/image/sup1.png"></li>
							<li></li>
							<li><img src="../../assets/image/sup2.png"></li>
							<li></li>
							<li><img src="../../assets/image/sup3.png"></li>
							<li></li>
							<li><img src="../../assets/image/sup4.png"></li>
						</ul>
						<ul>
							<li><span>{{lang('用户下单')}}</span></li>
							<li><span>{{lang('工厂备货')}}</span></li>
							<li><span>{{lang('国际运输')}}</span></li>
							<li><span>{{lang('直达用户')}}</span></li>
						</ul>
					</div>
				</div>
				<!--支付方式-->
				<div class="mdui-row mdui-color-white mdui-m-t-1 mdui-pannel mdui-p-b-0 ">
					<ul class="mdui-list-pay" style="padding-left: 4%;">
						<li class="mdui-list-item" style="padding-left: 0%;">
							<div class="mdui-list-item-content"><span class="label">{{lang('支持付款方式')}}</span></div>
							<i style="float: right;color: #000;font-style: normal">{{lang('信用卡')}}\{{lang('货到付款')}}\Paypal</i>
						</li>
					</ul>
				</div>
				<!--承诺-->
				<div class="mdui-row mdui-color-white mdui-m-t-1 mdui-pannel mdui-p-b-0 detail_promise">
					<ul class="mdui-list" style="padding-left: 4%;">
						<li class="mdui-list-item" style="padding-left: 0%;border-bottom: 0;">
							<div class="mdui-list-item-content custom-info">
								<div class="custom-info-details">
									<span v-if="detail_goods.money === 'NT'">支持7-11&amp;全家取货</span><span>{{lang('100%正品保障')}}</span><span>{{lang('7天无忧退换货')}}</span>
								</div>
							</div>
							<i class="mdui-icon material-icons mdui-text-color-grey-500">keyboard_arrow_right</i>
						</li>
					</ul>
				</div>
				<!--退货须知弹出框-->
				<div class="mdui-dialog mdui-dialog-open" id="return-product-dialog" style="height: auto !important;" v-if="false">
					<div class="mdui-dialog-title">{{lang('退换货须知')}}</div>
					<div class="mdui-dialog-content" style="height: 346px;">
						<p v-html="detail_goods.footer"></p>
					</div>
					<div class="mdui-dialog-actions mdui-dialog-actions-stacked" style="padding: 0">
						<button class="mdui-btn mdui-ripple" mdui-dialog-close="" style="background: #B4272D;color: #FFF;text-align: center;">{{lang('知道了')}}</button>
					</div>
				</div>
				<!--商品参数-->
				<div class="mdui-row mdui-color-white mdui-m-t-1 mdui-pannel mdui-p-b-0 mdui_parameters">
					<div class="mdui-pannel-header" style="color: #363636;padding-left: 0;font-size: 16px;line-height: 32px;margin-left:4%;border-bottom: 1px solid #EDEDED;">
						{{lang('商品参数')}}
					</div>
					<div class="mdui-pannel-content product-param" style="height: auto; max-height: initial;">
						<div>
							<table class="table table-bordered">
								<tbody>
								<tr>
									<td><span>{{detail_goods.option1_name}}</span></td>
									<td>
										<span v-for="(item,i) in specs.name_key_list" :key="i">{{item}}&nbsp;</span>
									</td>
								</tr>
								<tr v-if="specs.option1_key_list?specs.option1_key_list.length>0:false">
									<td><span>{{detail_goods.option2_name}}</span></td>
									<td>
										<span v-for="(item,i) in specs.option1_key_list" :key="i">{{item}}&nbsp;</span>
									</td>
								</tr>
								<tr v-if="specs.option2_key_list?specs.option2_key_list.length>0:false">
									<td><span>{{detail_goods.option3_name}}</span></td>
									<td>
										<span v-for="(item,i) in specs.option2_key_list" :key="i">{{item}}&nbsp;</span>
									</td>
								</tr>
								</tbody>
							</table>
						</div>
					</div>
				</div>
				<!--详情图-->
				<div id="summary" class="mdui-row mdui-color-white mdui-m-t-1 mdui-pannel mdui-p-b-0" v-show="!show">
					<div class="data-goods-imgs" v-html="detail_goods.content"></div>
				</div>
				<!--猜你喜欢-->
				<div class="popularity_goods" v-if="detail_goods.likes&&detail_goods.likes.length>0&&false">
					<div class="mdui-header" style="border-bottom: 1px solid #EDEDED;">
						<div class="mdui-header-title">
							<span>{{lang('人气爆品')}}</span>
						</div>
					</div>
					<likeGoods :like_goods="detail_goods.likes"></likeGoods>
				</div>
				<min-footer/>
				<!--规格选择框-->
				<div class="popup" v-if="spec_toggle">
					<div class="background" @click="spec_toggle=false"></div>
					<div class="xm-product">
						<Spec_cart :cart="cart" :specs="specs" @cartwin="spec_toggle=false" @prod="elect" :gifts="gifts" :promotion_flag="promotion_flag" :data="detail_goods"></Spec_cart>
						<div class="close" @click="spec_toggle=false">
							<img src="../../assets/image/detail/close.png">
						</div>
					</div>
				</div>
				<!--底部-->
				<div class="nav-car">
					<div class="cart">
						<div class="car_item">
							<div @click="kfShow()">
								<img src="../../assets/image/logo.png">
							</div>
							<transition name="fade" :kfshow.sync="kfshow">
								<customerService :data="siteInfo" v-if="kfshow"></customerService>
							</transition>
						</div>
						<router-link class="car_item cart_shop" :to="{ name: 'shoppingCart' }">
							<div>
								<div style="position: relative">
									<img src="../../assets/image/detail/car.png">
									<span>{{allnum}}</span>
								</div>
								<div class="cartTime" style="font-size: 12px;text-align: right;" v-show="addcart_flag && cart.list.length>0"><p>{{cart_m}}:{{cart_s}}</p>
									<p>{{lang('商品將保留')}}商品將保留</p></div>
							</div>
						</router-link>
					</div>
					<div class="cart buy-cart">
						<div class="buy" @click="spec_toggle=true;promotion_flag=false" :class="!detail_goods.time_price?'add-car-block':''">
							<div>{{detail_goods.money}}{{detail_goods.price}}</div>
							{{lang('出清')}}
						</div>
						<div class="add-car" @click="spec_toggle=true;promotion_flag=true" v-show="detail_goods.time_price">
							<div>{{detail_goods.money}}{{detail_goods.price - detail_goods.time_price}}</div>
							{{lang('限时出清')}}
						</div>
					</div>
				</div>
				<!--回到顶部-->
				<div id="toTop" style="" v-show="top_flag"><a href="#top"><img src="../../assets/image/detail/top.png"></a>
				</div>
				<!--浏览记录-->
				<div style="" v-show="foot_flag" class="footprint">
					<div class="shade">
						<div class="foot_list">
							<div class="colse_footprint" @click="foot_flag=false;"><img
									src="../../assets/image/icon-close2.png" alt=""></div>
							<div class="foot_title">{{lang('浏览过商品')}}({{historyInfo.length}})</div>
							<div class="foot_info">
								<router-link :to="'/detail/'+item.typeid+'/'+item.id" v-for="(item,index) of historyInfo" :key="index">
									<div>
										<div class="footprint_img">
											<img :src="item.head_imgs?config.cdnbase+item.head_imgs[0]:''" alt="" height="120" width="120">
										</div>
										<div class="footprint_info">
											<div class="footprint_name">{{item.sitename}}</div>
											<div class="footprint_price">
												<span class="footprint_price1">{{item.money}}{{item.time_price?item.price-item.time_price:item.price}}</span>
												<del class="footprint_oldprice">{{item.money}}{{item.oldprice}}</del>
											</div>
										</div>
									</div>
								</router-link>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
  import {goods} from '../../lib/goods.js'
  import customerService from '../../components/commen/customerService.vue'
  import likeGoods from '../../components/commen/likeGoods.vue'
  import Spec_cart from '../../components/commen/spec_cart.vue'
  import {mapState} from 'vuex'
  import Swiper from "swiper";
	// import moment from 'moment/moment'

	export default {
    data () {
      return {
				goodVisibility :true,
				kfshow: false,
        spec_toggle: false, /* 规格选择框控制器 */
        detail_goods: {}, /* 商品信息 */
        detail_spec: [], /* 商品规格 */
        promotion_flag: true, // 判读当前点开的是限时价还是原价true限时false原价
        gifts: [],
        top_flag: false, /* 返回顶部是否显示 */
        show_flag: false,
        did: '',
        t: 0,
        d: 0,
        h: 0,
        m: 0,
        s: 0,
        cart_m: 0,
        cart_s: 0,
        cdown_flag: false,
        addcart_flag: false,
        foot_flag: false,
        specs: {},
        cart: {
          list: [],
          gift: [],
          money: '',
          add_time: 0
        },
        prod: {},
        isActive: 0,
        isSelect: false,
        isShow: false, // 分类页面显示隐藏
        show: false, // 详情显示判断
        historyInfo: [],
//        siteInfo: this.$store.state.siteInfo,
//        lang: this.$store.state.language,
        firstLists: '',
        countdown:''
      }
    },
    methods: {
      goBack () {
				if(window.history.length <= 1){
					this.$router.push({path:'/'})
				}else{
					window.history.go(-1)
				}
			},
      kfShow(){
        this.kfshow = !this.kfshow
      },
      async get_detail_info () {
        // 商品信息
        let list = await goods.get_detail_info(this)
        if (!list.Error) {
          this.detail_goods = list
            document.title = list.sitename
          this.detail_goods.typeid = this.$router.history.current.params.typeid
          this.cart.money = list.money
          this.countdown = list.countdown
          // 商品规格
          let data = list.GoodsSpecs
          this.detail_spec = data
          this.specs = goods.format(this.detail_spec)
          let history = JSON.parse(localStorage.getItem('history'))
          if (history) {
            if (history.length >= 9) history.shift()
            let num = history.filter((item) => {
              return item.goods_id === this.detail_goods.goods_id && item.typeid === this.detail_goods.typeid && item.id === this.detail_goods.id
            }).length
            if (num === 0) {
              history.push(this.detail_goods)
            }
          } else {
            history = [this.detail_goods]
          }
          await localStorage.setItem('history', await JSON.stringify(history))
          this.historyInfo = JSON.parse(localStorage.getItem('history'))
					await goods.update_page_viewsc(this.detail_goods.goods_id)
        }else{
          this.$router.push('/404');
        }
      },
      elect(prods) { //回调方法，接收子组件传的参数
        this.prod = prods
      },
      // 倒计时
      GetRTime () {
        let transTime = this.countdown?this.countdown.replace(/-/g, '/'):0;
				// let transTime = moment(this.detail_goods.countdown).format('YYYY/MM/DD')
				let EndTime = new Date(transTime)
        EndTime.setDate(EndTime.getDate())
        let NowTime = new Date()
        EndTime.setDate(EndTime.getDate())
        let t = EndTime.getTime() - NowTime.getTime()
        if (t <= 0) {
          this.cdown_flag = false
        } else {
          this.d = 0
          this.h = 0
          this.m = 0
          this.s = 0
          if (t >= 0) {
            this.d = Math.floor(t / 1000 / 60 / 60 / 24);
            if (this.d < 10) {
              this.d = '0' + Math.floor(t / 1000 / 60 / 60 / 24);
            }
            this.h = Math.floor(t / 1000 / 60 / 60 % 24);
            if (this.h < 10) {
              this.h = '0' + Math.floor(t / 1000 / 60 / 60 % 24);
            }
            this.m = Math.floor(t / 1000 / 60 % 60);
            if (this.m < 10) {
              this.m = '0' + Math.floor(t / 1000 / 60 % 60);
            }
            this.s = Math.floor(t / 1000 % 60);
            if (this.s < 10) {
              this.s = '0' + Math.floor(t / 1000 % 60);
            }
          }
          this.cdown_flag = true;
        }
      },
      // 加入购物车
      cart_GetRTime () {
        let EndTime = new Date(this.cart.add_time)
        EndTime.setDate(EndTime.getDate())
        let NowTime = new Date()
        EndTime.setDate(EndTime.getDate())
        let t = EndTime.getTime() - NowTime.getTime()
        if (t <= 0) {
          this.addcart_flag = false
        } else {
          this.cart_m = 0
          this.cart_s = 0
          if (t >= 0) {
            this.cart_m = Math.floor(t / 1000 / 60 % 60)
            if (this.cart_m < 10) {
              this.cart_m = '0' + Math.floor(t / 1000 / 60 % 60)
            }
            this.cart_s = Math.floor(t / 1000 % 60)
            if (this.cart_s < 10) {
              this.cart_s = '0' + Math.floor(t / 1000 % 60)
            }
          }
          this.addcart_flag = true
        }
      },
      decNum: function () {
        this.prod.number > 1 ? this.prod.number -= 1 : this.prod.number = 1
        for (let i in this.gifts) {
          for (let j in this.gifts[i]) {
            this.gifts[i][j].number > 1 ? this.gifts[i][j].number -= 1 : this.gifts[i][j].number = 1
          }
        }
      },
      addNum: function () {
        this.prod.number += 1
        for (let i in this.gifts) {
          for (let j in this.gifts[i]) {
            this.gifts[i][j].number += 1
          }
        }
      },
      handleScroll(){
        var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
        if (scrollTop > 300) {
          this.top_flag = true
        } else {
          this.top_flag = false
        }
      }
    },
    async created () {
      this.did = this.$route.params.did
      this.get_detail_info()
      this.historyInfo = JSON.parse(localStorage.getItem('history')) || ''
      const cart_info = localStorage.getItem('cart_info')
      if (cart_info) {
        this.cart = JSON.parse(localStorage.getItem('cart_info'))
      }
      this.logo = this.$store.state.siteInfo.logo
      // 倒计时
      setInterval(this.GetRTime, 1000)
      if (this.d === 0 && this.h === 0 && this.m === 0 && this.s === 0) {
        clearInterval(this.GetRTime)
      }
      setInterval(this.cart_GetRTime, 300)
      if (this.cart_m === 0 && this.cart_s === 0) {
        clearInterval(this.cart_GetRTime)
      }
    },
    mounted () {
      window.addEventListener('scroll', this.handleScroll)
    },
    components: {
      likeGoods,
      Spec_cart,
      customerService
    },
    computed: {
      ...mapState(['siteInfo', 'classInfo', 'language', 'config']),
      lang(){
        return function (src) {
          return this.language ? this.language[src] : ''
        }
      },
      allnum(){
        let allnum = 0;
        for (let item of this.cart.list) {
          allnum += item.number;
        }
        return allnum;
      }
    },
    watch: {
      'detail_goods':function(){
				this.$nextTick(()=>{
					this.swiper =  new Swiper('.swiper-container',{
						pagination : '.pagination',
						autoplay: 3500,
						loop:true
					})
        })
      },
      //监听相同路由下参数变化的时候，从而实现异步刷新
      '$route'(to, from) {
        if (to.hash !== "#mdui-dialog" && from.hash !== "#mdui-dialog") {
          //做一些路由变化的响应
					this.goodVisibility = false   //在html里通过v-if='goodVisibility '来控制
          //重新获取did
          this.did = this.$route.params.did
          //将数据置空
          this.detail_goods = {}
			/*商品信息*/
          this.detail_spec = []
			/*商品规格*/
          this.foot_flag = false
          //重新获取数据
          this.get_detail_info()
					this.$nextTick(() => {
						this.goodVisibility = true
					})
        }
      },
      'foot_flag' () {
        if (this.foot_flag === true) {
          document.body.style.overflowY = 'hidden'
        } else {
          document.body.style.overflowY = 'auto'
        }
      }
    }
  }
</script>
<style lang="stylus">
	@media (max-width: 414px) {
		.swiper-slide, .swiper-container {
			width: 100%;
			height: 375px !important;
		}
	}
	@media (min-width: 768px) {
		.swiper-slide, .swiper-container {
			width: 100%;
			height: 768px !important;
		}
	}
  .data-goods-imgs{
    float left
  }
  .data-goods-imgs>p{
    display: block
    text-align: center;
    line-height: 2em;
    width: 100%;
    margin: 0 auto;
    font-size: 13px;
    padding-bottom: 10px;
  }
  .data-goods-imgs img{
    width 100% !important
    display block
  }
  .data-goods-imgs p iframe{
    width: 100% !important;
  }
  .app-detail .data-goods-imgs iframe,.app-detail .data-goods-imgs video{
    width: 100% !important;
    height: 432px !important;
  }
  /*视频兼容性*/
  @media screen and (max-width: 361px) {
	.app-detail .data-goods-imgs iframe,.app-detail .data-goods-imgs video{
      height:202px !important;
    }
  }
  @media screen and (min-width:361px) and (max-width:376px) {
    .app-detail .data-goods-imgs iframe,.app-detail .data-goods-imgs video{
      height:211px !important;
    }
  }
  @media screen and (min-width:376px) and (max-width:414px) {
    .app-detail .data-goods-imgs iframe,.app-detail .data-goods-imgs video{
      height:230px !important;
    }
  }
  #swipe .swiper-slide iframe,#swipe .swiper-slide video{
	height 100%;
  }
</style>
<style scoped lang="stylus">
  /*足迹*/
  .footprint
    .shade
      position fixed
      top:0
      left 0
      right 0
      bottom 0
      background rgba(0,0,0,0.5)
      z-index 55
      max-width 768px
      margin auto
      .foot_list
